import React from 'react'
import { Col, Row } from 'react-bootstrap'
import { teamData } from '../../constants/teamData'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useNavigate } from 'react-router-dom'
import { route } from '../../constants/AllRoutes';


const OurTeam = ({ isDark }) => {

    const navigation = useNavigate();

    return (
        <section className='our-team'>
            <Row className='justify-content-between'>
                <Col xl={4} lg={5} md={12} sm={12}>
                    <div className="sec_title">
                        Meet<br /> Our Team
                    </div>
                    <div className="sec_dec">
                        <p>We are talented individuals who are passionate about bringing ideas to life. With a diverse range of backgrounds and skill sets, we collaborate to produce effective solutions for our clients.</p>
                        <p>Together, our creative team is committed to delivering impactful work that exceeds expectations.</p>
                    </div>
                    <div className="sec_content">
                        Our <span>Results</span> that <br />Impress <span>You.</span>
                    </div>
                </Col>
                <Col xl={6} lg={7} md={12} sm={12}>
                    <div className="grid_sec">
                        {
                            teamData?.map((team, index) => {
                                return (
                                    <div className='team_box' key={index}>
                                        <div className="profile-Image">
                                            <img src={team?.profileImage} alt="" />
                                        </div>
                                        <div className='profile_content'>
                                            <div className="profile-details">
                                                <div className="profile-name" onClick={() => navigation(route.viewProfile(team?._id))}>{team?.name}</div>
                                                <div className="profile-designation">{team?.designation}</div>
                                                {
                                                    team?.links &&
                                                    <div className='link-grp'>
                                                        {
                                                            team?.links?.map((link, index) => {
                                                                return (
                                                                    <a href={link?.url} key={index}><FontAwesomeIcon icon={link?.icon} /></a>
                                                                )
                                                            })
                                                        }
                                                    </div>
                                                }
                                            </div>
                                        </div>
                                    </div>
                                )
                            })
                        }
                    </div>
                </Col>
            </Row>
        </section>
    )
}

export default OurTeam